// The features page
//
// Styleguide features
html {
  &.features {
    body {
      @include features--gradients;
      @include features--menu-active;

      .features {
        @include features;
      }

      .horizontal-line {
        &.for-footer {
          @include colors(black-on-white);
          @include border(normal, bottom);
          border-color: $gray-format;
          padding-top: $lem * 4.165;
        }
      }
    }
  }
}
