@mixin menu-item--horizontal {
  .link {
    @include text-style(navigation);
    @include link(gray-format-on-transparent, white-on-transparent, gray-format-on-transparent);
  }

  .button {
    @include button--squared;

    .link {
      @include link(white-on-transparent, white-on-transparent, white-on-transparent);
    }
  }
}
