@mixin menu-item--vertical {
  .link {
    @include text-style(hero);
    @include link(blue-on-transparent, blue-on-transparent, blue-on-transparent);
  }

  .button {
    .link {
      @include text-style(headline-1);
      @include link(white-on-transparent, white-on-transparent, white-on-transparent);
    }
  }

  .icon {
    @include icon($lem * 1.5,  $lem * 1.5, $gray-footer);

    .link {
      @include text-style(default);
    }
  }


  @include mobile {
    .link {
      @include text-style(headline-1);
    }

    .button {
      .link {
        @include text-style(headline-2);
      }
    }

    .icon {
      @include icon($lem,  $lem, $gray-footer);
    }
  }
}
