@mixin menu__items--vertical {
  @include section-padding;
  padding-top: $lem * 4;

  display: flex;
  flex-direction: column;
  justify-content: space-around;

  @include colors(white-on-black);
  opacity: .99;

  .common,
  .buttons {
    flex-direction: column;

    .menu-item {
      + .menu-item {
        margin-top: $lem;
      }
    }
  }

  .menu-item {
    @include menu-item--vertical;
  }

  .icons {
    display: flex;
    flex-wrap: nowrap;
    margin-bottom: $lem;

    .menu-item {
      + .menu-item {
        margin-left: $lem;
      }
    }
  }
}
