@mixin menu__items--horizontal {
  display: flex;
  justify-content: center;
  align-items: center;


  $margin: $lem + $lem / 2;

  .menu-item {
    @include menu-item--horizontal;

    + .menu-item {
      margin-left: $margin;
    }
  }

  .buttons {
    margin-left: $margin;

    .menu-item {
      + .menu-item {
        margin-left: $margin / 2;
      }
    }
  }

  .icons {
    display: none;
  }
}
